<template>
  <div>
    <div style="display: flex; justify-content: space-between; margin-top: 10px">
      <el-button type="primary" icon="el-icon-plus" size="mini" @click="showAddSal">add</el-button>
      <el-button type="success" icon="el-icon-refresh" size="mini" @click="initSals"></el-button>
    </div>
    <div style="margin-top: 10px">
      <el-table :data="sals" border stripe>
        <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column prop="name" label="账套名称" width="120"></el-table-column>
          <el-table-column prop="basicSalary" label="基本工资" width="70"></el-table-column>
          <el-table-column prop="trafficSalary" label="交通补助" width="70"></el-table-column>
          <el-table-column prop="lunchSalary" label="午餐补助" width="70"></el-table-column>
          <el-table-column prop="bonus" label="奖金" width="70"></el-table-column>
          <el-table-column prop="createDate" label="创建时间" width="100"></el-table-column>
          <el-table-column label="养老金" align="center">
            <el-table-column prop="pensionPer" label="养老金比率" width="90"></el-table-column>
            <el-table-column prop="pensionBase" label="养老金基数" width="90"></el-table-column>
          </el-table-column>
          <el-table-column label="医疗保险" align="center">
            <el-table-column prop="medicalPer" label="医疗保险比率" width="100"></el-table-column>
            <el-table-column prop="medicalBase" label="医疗保险基数" width="100"></el-table-column>
          </el-table-column>
          <el-table-column label="公积金" align="center">
            <el-table-column prop="accumulationFundPer" label="公积金比率" width="90"></el-table-column>
            <el-table-column prop="accumulationFundBase" label="公积金基数" width="90"></el-table-column>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" @click="showEditSal(scope.row)">edit</el-button>
              <el-button type="danger" @click="deleteSal(scope.row)">delete</el-button>
            </template>
          </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="30%">
      <div style="display: flex; justify-content: space-around; align-items: center">
        <el-steps direction="vertical" :active="activeItemIndex">
          <el-step :title="itemName" v-for="(itemName, index) in salItemName" :key="index"></el-step>
        </el-steps>
        <el-input
          v-model="sal[title]"
          :placeholder="'请输入'+salItemName[index]+'...'"
          v-for="(value, title, index) in sal"
          :key="index"
          v-show="activeItemIndex==index"
          style="width: 200px"></el-input>
      </div>
      <span slot="footer">
        <el-button @click="prevStep">{{activeItemIndex==10?'cancel':'prev'}}</el-button>
        <el-button type="primary" @click="nextStep">{{activeItemIndex==10?'confirm':'next'}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'SalSob',
  data () {
    return {
      sals: [],
      dialogVisible: false,
      salItemName: [
        '账套名称',
        '基本工资',
        '交通补助',
        '午餐补助',
        '奖金',
        '养老金比率',
        '养老金基数',
        '医疗保险基数',
        '医疗保险比率',
        '公积金基数',
        '公积金比率'
      ],
      activeItemIndex: 1,
      sal: {
        id: null,
        name: '',
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0
      },
      dialogTitle: ''
    }
  },
  mounted () {
    this.initSals()
  },
  methods: {
    initSals () {
      this.getRequest('/salary/sob/').then(resp => {
        if (resp) {
          this.sals = resp
        }
      })
    },
    showAddSal () {
      this.dialogTitle = '添加工资账套'
      this.sal = {
        name: '',
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0
      }
      this.activeItemIndex = 1
      this.dialogVisible = !this.dialogVisible
    },
    nextStep () {
      if (this.activeItemIndex === 10) {
        if (this.sal.id) {
          console.log(this.sal)
          this.putRequest('/salary/sob/', this.sal).then(resp => {
            if (resp) {
              this.initSals()
              this.dialogVisible = !this.dialogVisible
            }
          })
        } else {
          this.postRequest('/salary/sob/', this.sal).then(resp => {
            if (resp) {
              this.initSals()
              this.dialogVisible = !this.dialogVisible
            }
          })
        }
        return
      }
      this.activeItemIndex++
    },
    prevStep () {
      if (this.activeItemIndex === 0) {
        return
      } else if (this.activeItemIndex === 10) {
        this.dialogVisible = !this.dialogVisible
        return
      }
      this.activeItemIndex--
    },
    deleteSal (data) {
      this.$confirm('此操作将删除[' + data.name + ']的相关信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/salary/sob/' + data.id).then(resp => {
          if (resp) {
            this.initSals()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    showEditSal (data) {
      this.dialogTitle = '更新工资账套'
      this.sal = {
        id: data.id,
        name: data.name,
        basicSalary: data.basicSalary,
        trafficSalary: data.trafficSalary,
        lunchSalary: data.lunchSalary,
        bonus: data.bonus,
        pensionPer: data.pensionPer,
        pensionBase: data.pensionBase,
        medicalPer: data.medicalPer,
        medicalBase: data.medicalBase,
        accumulationFundPer: data.accumulationFundPer,
        accumulationFundBase: data.accumulationFundBase
      }
      this.activeItemIndex = 1
      this.dialogVisible = !this.dialogVisible
    }
  }
}
</script>

